import api from "@constants/api";
import Service from "@services/request";

export const getData = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_BRANCH.method,
      path: api.LIST_BRANCH.path,
      data: params,
    }).then((response) => {
      const dataStaff = [];
      if (response?.data?.data?.length > 0) {
        const promiseStaff = response?.data?.data.map(async (item, index) => {
          if (item?.staffId) {
            await Service.send({
              method: api.GET_STAFF.method,
              path: api.GET_STAFF.path,
              query: { id: item?.staffId },
            }).then((resStaff) => {
              dataStaff[index] = {
                ...item,
                staff: resStaff?.data,
              };
            });
          } else {
            dataStaff[index] = {
              ...item,
              staff: {},
            };
          }
        });
        Promise.all(promiseStaff).then(() => {
          dispatch({
            type: "GET_DATA_BRANCH",
            data: dataStaff,
            totalPages: response?.data?.total,
            params,
          });
        });
      }
      dispatch({
        type: "GET_DATA_BRANCH",
        data: response?.data?.data,
        totalPages: response?.data?.total,
        params,
      });
    });
  };
};

