export const getStaff = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_STAFF.method,
      path: api.LIST_STAFF.path,
      data: params,
    }).then((response) => {
      dispatch({
        type: "GET_DATA_STAFF",
        data: response?.data?.data,
        totalPages: response?.data?.total,
        params,
      });
    });
  };
};

export const getStaffId = (staffId) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_STAFF.method,
      path: api.GET_STAFF.path,
      query: { id: staffId },
    })
      .then((response) => {
        dispatch({
          type: "GET_STAFF",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};

export const getBranch = (id) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_BRANCH.method,
      path: api.GET_BRANCH.path,
      query: { id },
    })
      .then((response) => {
        dispatch({
          type: "GET_BRANCH",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};

export const addBranch = (Branch) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.CREATE_BRANCH.method,
      path: api.CREATE_BRANCH.path,
      data: Branch,
    })
      .then((response) => {
        dispatch({
          type: "ADD_BRANCH",
          response,
        });
      })
      .catch((err) =>
        dispatch({
          type: "ADD_BRANCH",
          err,
        })
      );
  };
};
export const updateBranch = (Branch) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.UPDATE_BRANCH.method,
      path: api.UPDATE_BRANCH.path,
      data: Branch,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_BRANCH",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_BRANCH",
          err,
        })
      );
  };
};
