import { useEffect, useState } from "react";
import { FormattedMessage, injectIntl } from "react-intl";

import "react-phone-number-input/style.css";

import { isObjEmpty } from "@utils";

import classnames from "classnames";
import { Controller, useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router-dom";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import validateOptions from "@constants/validate";
import { getStaff, updateBranch, getStaffId } from "../store/action";
import classNames from "classnames";
import Select from "react-select";
import draftToHtml from "draftjs-to-html";
import { convertToRaw, EditorState } from "draft-js";
import { Editor } from "react-draft-wysiwyg";
import { htmlToDraftUtil } from "@utils";

const BranchTab = ({ selected, intl }) => {
  const store = useSelector((state) => state.branch);
  const { register, errors, control, setError, setValue, handleSubmit } =
    useForm();
  const history = useHistory();
  const [BranchData, setBranchData] = useState(null);
  const lang = useSelector((state) => state.common.language);
  const dispatch = useDispatch();
  const [status, setStatus] = useState(null);
  const [disable, setDisable] = useState(false);
  const { data } = useSelector((state) => state.staffs);
  const { id } = useParams();
  const [selectStudent, setSelectStudent] = useState(null);
  const EditBranchOptions = validateOptions.EditBranchOptions;

  const options = {
    value: store?.staffDetail?.id,
    label: store?.staffDetail?.username,
  };
  const [changcate, setChangcate] = useState();
  const [valueContent, setValueContent] = useState(EditorState.createEmpty());

  useEffect(() => {
    if (selected) {
      setValueContent(htmlToDraftUtil(selected?.description || " "));
    }
  }, [selected]);
  const [changeSelect, setChangeSelect] = useState(changcate);
  const changeCate = (e) => {
    setChangeSelect(e);
  };
  useEffect(() => {
    if (store) {
      setChangcate(options);
    }
  }, [store]);
  useEffect(() => {
    if (changcate) {
      setChangeSelect(changcate);
    }
  }, [changcate]);

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  useEffect(() => {
    dispatch(getStaffId(selected?.staffId));
  }, [dispatch, selected?.staffId]);

  useEffect(() => {
    dispatch(
      getStaff({
        filter: {
          // status: filterStatus.value || undefined,
          // name: searchName || undefined,
          status: 1,
          // lang,
        },
        // skip: 0,
        // limit: 100,
        order: {
          key: "createdAt",
          value: "asc",
        },
      })
    );
  }, []);

  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        BranchData !== null &&
        selected.id !== BranchData.id)
    ) {
      setBranchData(selected);
    }
  }, [selected, BranchData]);

  useEffect(() => {
    if (store.status === 200) {
      history.push("/apps/cms/branch/list");
    }
  }, [store.status, history]);

  const onSubmit = async (values) => {
    const description = draftToHtml(
      convertToRaw(valueContent.getCurrentContent())
    );
    if (isObjEmpty(errors)) {
      setDisable(true);
      dispatch(
        updateBranch({
          id: parseInt(id),
          data: {
            ...values,
            name: values?.name || "",
            code_branch: values?.code_branch || "",
            addpress: values?.addpress || "",
            phone: values?.phone || "",
            email: values?.email || "",
            description: description || "",
            staffId: parseInt(changeSelect?.value),
            status: parseInt(values.status),
            lang,
          },
        })
      );
    }
  };

  useEffect(() => {
    if (BranchData) {
      setStatus(BranchData.status);
    }
  }, [BranchData]);
  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.name} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label for="name">
              <FormattedMessage id="Branch name" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="name"
              id="name"
              innerRef={register(EditBranchOptions.name)}
              onBlur={() => {
                let name = document.getElementById("name");
                if (name && name.value) {
                  name.value = name.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["name"] })}
              defaultValue={BranchData && BranchData.name}
            />
            <small className="text-danger">
              {errors?.name && errors.name.message}
            </small>
            {errors?.name?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid kind name" />
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Label for="code_branch">
              <FormattedMessage id="Branch Code" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="code_branch"
              id="code_branch"
              innerRef={register(EditBranchOptions.code_branch)}
              onBlur={() => {
                let code_branch = document.getElementById("code_branch");
                if (code_branch && code_branch.value) {
                  code_branch.value = code_branch.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["code_branch"] })}
              defaultValue={BranchData && BranchData.code_branch}
            />
            <small className="text-danger">
              {errors?.code_branch && errors.code_branch.message}
            </small>
            {errors?.code_branch?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid kind name" />
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Label for="description">
              <FormattedMessage id="Description" />{" "}
            </Label>
            <Editor
              stripPastedStyles={true}
              editorState={valueContent}
              onEditorStateChange={(data) => setValueContent(data)}
              name="description"
              innerRef={register}
            />
          </FormGroup>

          <FormGroup>
            <Label for="addpress">
              <FormattedMessage id="address" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="addpress"
              id="addpress"
              innerRef={register(EditBranchOptions.addpress)}
              onBlur={() => {
                let addpress = document.getElementById("addpress");
                if (addpress && addpress.value) {
                  addpress.value = addpress.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["addpress"] })}
              defaultValue={BranchData && BranchData.addpress}
            />
            <small className="text-danger">
              {errors?.addpress && errors.addpress.message}
            </small>
            {errors?.addpress?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid kind name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="email">
              <FormattedMessage id="Email" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="email"
              id="email"
              innerRef={register(EditBranchOptions.email)}
              onBlur={() => {
                let email = document.getElementById("email");
                if (email && email.value) {
                  email.value = email.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["email"] })}
              defaultValue={BranchData && BranchData.email}
            />
            <small className="text-danger">
              {errors?.email && errors.email.message}
            </small>
            {errors?.email?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid kind name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="phone">
              <FormattedMessage id="phoneNumber" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="phone"
              id="phone"
              innerRef={register(EditBranchOptions.phone)}
              onBlur={() => {
                let phone = document.getElementById("phone");
                if (phone && phone.value) {
                  phone.value = phone.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["phone"] })}
              defaultValue={BranchData && BranchData.phone}
            />
            <small className="text-danger">
              {errors?.phone && errors.phone.message}
            </small>
            {errors?.phone?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid kind name" />
              </small>
            )}
          </FormGroup>

          {/* <FormGroup>
            <Label>
              <FormattedMessage id="Staff" />
              <span className="text-danger"> * </span>
            </Label>

            <Controller
              control={control}
              name="staffId"
              render={({ field }) => {
                return (
                  <Select
                    id="staffId"
                    innerRef={register}
                    name="staffId"
                    placeholder={<FormattedMessage id="Select Student" />}
                    defaultValue={selectStudent}
                    className={classnames(
                      "react-select",
                      !changeSelect
                        ? {
                            "is-invalid": errors["staffId"],
                          }
                        : ""
                    )}
                    options={data?.map((item, index) => {
                      return {
                        value: item?.id,
                        label: item.first_name + " " + item.last_name || "",
                      };
                    })}
                    classNamePrefix="select"
                    {...field}
                    onChange={(e) => {
                      setError("staffId", "");
                      setValue("staffId", e?.value);
                      setChangeSelect(e?.value);
                    }}
                  />
                );
              }}
            ></Controller>

            <small className="text-danger">
              {errors?.staffId && errors.staffId.message}
            </small>
          </FormGroup> */}

          <FormGroup>
            <Label>
              <FormattedMessage id="Staff" />
              <span className="text-danger"> * </span>
            </Label>
            <Select
              isClearable={false}
              onChange={(e) => changeCate(e)}
              innerRef={register({ required: true })}
              name="staffId"
              value={changeSelect}
              placeholder={<FormattedMessage id="Select..." />}
              options={data?.map((item, index) => {
                return {
                  value: item?.id,
                  label: item.first_name + " " + item.last_name || "",
                };
              })}
              className="react-select"
              classNamePrefix="select"
            />
            <small className="text-danger">
              {errors?.staffId && errors.staffId.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="status">
              <FormattedMessage id="Status" />
            </Label>

            <Input
              type="select"
              name="status"
              value={status}
              onChange={(e) => setStatus(e.target.value)}
              id="status"
              innerRef={register({ required: true })}
            >
              <option value="1">{intl.formatMessage({ id: "Active" })}</option>
              <option value="0">{intl.formatMessage({ id: "Blocked" })}</option>
            </Input>
          </FormGroup>

          <div style={{ textAlign: "end" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onClick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(BranchTab);
